	module.exports = {"title":"Real time updates with Relay and Rails' ActionCable","date":"2016-05-29T10:00:00.000Z","description":"Setting up live updates with Rails' ActionCable, GraphQL and Relay","body":"<meta name=\"twitter:card\" content=\"summary_large_image\">\n<meta name=\"twitter:site\" content=\"@__xuorig__\">\n<meta name=\"twitter:creator\" content=\"@__xuorig__\">\n<meta name=\"twitter:title\" content=\"Real time updates with Relay and Rails' ActionCable\">\n<meta name=\"twitter:description\" content=\"Setting up live updates with Rails' ActionCable, GraphQL and Relay\">\n<meta name=\"twitter:image\" content=\"http://mgiroux.me/assets/images/realtimerelay.gif\">\n<p>To me, the only thing missing to the GraphQL ecosystem and spec today are live updates. When our client applications require live updates from the server for data they care about, we need some sort of mechanism and the GraphQL team has talked about introducing a third operation besides <code>query</code> and <code>mutation</code>: the <a href=\"http://GraphQL.org/blog/subscriptions-in-GraphQL-and-relay/\">subscriptions</a>.</p>\n<p>Unfortunatly for us, the spec hasn’t described the “way” to do it just yet and no real implementation or spec of <code>subscriptions</code> exists. The good news is that it’s still possible to have real time updates in our Relay applications using our current existing strategies, such as a standard WebSocket connection.</p>\n<p>This weekend I took that challenge and at the same time gave Rails’ <code>ActionCable</code> for a ride.</p>\n<h2>A bit of context on what we’ll build</h2>\n<p>I’m building <a href=\"https://github.com/reviewbuddy/review-buddy\">ReviewBuddy</a> on my spare time. ReviewBuddy is a small app to keep track of your own pull requests, and other pull requests that you need to review. Now, when somebody updates these pull requests, I’d like the pull requests on review buddy to be udpated too. It would be even better if it was in real time!</p>\n<h2>ActionCable</h2>\n<p>Let’s setup ActionCable first. Since my client app doesn’t live inside my Rails GraphQL API, we’ll need to whitelist that domain for ActionCable to work:</p>\n<pre><code class=\"language-ruby\">  <span class=\"hljs-comment\"># config/environments/development.rb</span>\n  config.action_cable.allowed_request_origins = [<span class=\"hljs-string\">'http://localhost:8080'</span>]\n</code></pre>\n<p>ActionCable works using “Channels”. For anything we need to stream to client, we have to define a <code>Channel</code>. For PullRequests, I’ve created a <code>PullRequestsChannel</code>:</p>\n<pre><code class=\"language-ruby\"><span class=\"hljs-comment\"># app/channels/pull_requests_channel.rb</span>\n<span class=\"hljs-class\"><span class=\"hljs-keyword\">class</span> <span class=\"hljs-title\">PullRequestsChannel</span> &lt; ApplicationCable::Channel</span>\n  <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">subscribed</span></span>\n    stream_from <span class=\"hljs-string\">'pull_requests'</span>\n  <span class=\"hljs-keyword\">end</span>\n<span class=\"hljs-keyword\">end</span>\n</code></pre>\n<p>The only thing left to do is to actually stream data when something has changed! We could simply have an <code>after_commit</code> that streams the pull request when something changed, but that’s always a bad desision ;). let’s just broadcast from where we modify the pull request.</p>\n<p>All the Pull Request updates happen in a job. On perform, we find the PullRequest from the Github webhook params, and we update what we care about.</p>\n<p>After a successful save, we broadcast the changes to the client using <code>ActionCable.server.broadcast</code>, using the <code>pull_requests</code> stream and our <code>pull_request</code> as a param.</p>\n<pre><code class=\"language-ruby\">  <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">perform</span><span class=\"hljs-params\">(params)</span></span>\n    pr = PullRequest.find_by(\n      <span class=\"hljs-symbol\">repo:</span> params[<span class=\"hljs-symbol\">:repository</span>][<span class=\"hljs-symbol\">:name</span>],\n      <span class=\"hljs-symbol\">owner:</span> params[<span class=\"hljs-symbol\">:repository</span>][<span class=\"hljs-symbol\">:owner</span>][<span class=\"hljs-symbol\">:login</span>],\n      <span class=\"hljs-symbol\">number:</span> params[<span class=\"hljs-symbol\">:number</span>],\n    )\n    <span class=\"hljs-keyword\">return</span> <span class=\"hljs-keyword\">unless</span> pr\n\n    pr.title = params[<span class=\"hljs-symbol\">:pull_request</span>][<span class=\"hljs-symbol\">:title</span>]\n    pr.merged = params[<span class=\"hljs-symbol\">:pull_request</span>][<span class=\"hljs-symbol\">:merged</span>]\n\n    <span class=\"hljs-keyword\">if</span> pr.save!\n      broadcast_pr_change(pr)\n    <span class=\"hljs-keyword\">end</span>\n  <span class=\"hljs-keyword\">end</span>\n\n  <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">broadcast_pr_change</span><span class=\"hljs-params\">(pr)</span></span>\n    ActionCable.server.broadcast <span class=\"hljs-string\">'pull_requests'</span>,\n      <span class=\"hljs-symbol\">pull_request:</span> {\n        <span class=\"hljs-symbol\">id:</span> pr.to_global_id.to_s,\n        <span class=\"hljs-symbol\">title:</span> pr.title\n      }\n  <span class=\"hljs-keyword\">end</span>\n</code></pre>\n<h2>Relay Client App</h2>\n<p>Now the hard thing was communicating with <code>ActionCable</code> in the <code>Relay</code> app, and also updating the <code>Relay</code> Store. <code>ActionCable</code> is really tied to <code>Rails</code>, and trying to use it with an external client side app + API was harder than expected.</p>\n<p>With the help of this <a href=\"https://github.com/NullVoxPopuli/action_cable_client#the-action-cable-protocol\">repo</a>, I was able to create own javascript client to implement the <code>ActionCable</code> protocol.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">default</span> <span class=\"hljs-class\"><span class=\"hljs-keyword\">class</span> <span class=\"hljs-title\">ActionCableClient</span> </span>{\n  <span class=\"hljs-keyword\">constructor</span>(uri, channel, onMessage) {\n    <span class=\"hljs-keyword\">this</span>.uri = uri;\n    <span class=\"hljs-keyword\">this</span>.channel = channel;\n    <span class=\"hljs-keyword\">this</span>.onMessage = onMessage;\n\n    <span class=\"hljs-keyword\">this</span>.socket = <span class=\"hljs-keyword\">new</span> WebSocket(uri);\n    <span class=\"hljs-keyword\">this</span>.socket.onmessage = <span class=\"hljs-keyword\">this</span>._handleMessage.bind(<span class=\"hljs-keyword\">this</span>);\n\n    <span class=\"hljs-keyword\">this</span>._onReadyPromise = <span class=\"hljs-keyword\">new</span> <span class=\"hljs-built_in\">Promise</span>(<span class=\"hljs-function\">(<span class=\"hljs-params\">resolve, reject</span>) =&gt;</span> {\n      <span class=\"hljs-keyword\">this</span>._resolveOnReady = resolve;\n      <span class=\"hljs-keyword\">this</span>._rejectOnReady= reject;\n    });\n  }\n\n  <span class=\"hljs-keyword\">static</span> connect(uri, channel, onMessage) {\n    <span class=\"hljs-keyword\">const</span> instance = <span class=\"hljs-keyword\">new</span> <span class=\"hljs-keyword\">this</span>(uri, channel, onMessage);\n    <span class=\"hljs-keyword\">return</span> instance.connect();\n  }\n\n  connect() {\n    <span class=\"hljs-keyword\">this</span>.socket.onopen = <span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> {\n      <span class=\"hljs-keyword\">this</span>.socket.send(<span class=\"hljs-keyword\">this</span>._getSubscribePayload())\n    };\n\n    <span class=\"hljs-keyword\">return</span> <span class=\"hljs-keyword\">this</span>._onReadyPromise;\n  }\n\n  _handleMessage(event) {\n    <span class=\"hljs-keyword\">const</span> msg = <span class=\"hljs-built_in\">JSON</span>.parse(event.data);\n    <span class=\"hljs-keyword\">if</span> (msg.type === <span class=\"hljs-string\">'confirm_subscription'</span>) {\n      <span class=\"hljs-keyword\">this</span>._resolveOnReady(<span class=\"hljs-keyword\">this</span>);\n    } <span class=\"hljs-keyword\">else</span> {\n      <span class=\"hljs-keyword\">this</span>.onMessage(msg);\n    }\n  }\n\n  _getSubscribePayload() {\n    <span class=\"hljs-keyword\">return</span> <span class=\"hljs-built_in\">JSON</span>.stringify({\n      <span class=\"hljs-attr\">command</span>: <span class=\"hljs-string\">'subscribe'</span>,\n      <span class=\"hljs-attr\">identifier</span>: <span class=\"hljs-built_in\">JSON</span>.stringify({\n        <span class=\"hljs-attr\">channel</span>: <span class=\"hljs-keyword\">this</span>.channel\n      })\n    });\n  }\n}\n</code></pre>\n<p>I’ve created a gist of that client right here <a href=\"https://gist.github.com/xuorig/b167b27f75c149b8da5837b79585169f\">https://gist.github.com/xuorig/b167b27f75c149b8da5837b79585169f</a>.</p>\n<p>And this is how it can be used inside your app:</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">const</span> myEventHandlerFunction = <span class=\"hljs-function\">(<span class=\"hljs-params\">event</span>) =&gt;</span> {\n  <span class=\"hljs-built_in\">console</span>.log(event.message);\n}\n\nActionCableClient.connect(\n  <span class=\"hljs-string\">'ws://localhost:3000/cable'</span>,\n  <span class=\"hljs-string\">'YourChannel'</span>,\n  myEventHandlerFunction\n).then(<span class=\"hljs-function\"><span class=\"hljs-params\">client</span> =&gt;</span> {\n  client is guaranteed to be initialized properly\n  when promise is resolved.\n});\n</code></pre>\n<p>With the client finished, we have all we need to start speaking with our API in real time. The only challenge left is how to update the RelayStore from another source. This is where having an actual subscription would be amazing but for now we have to choose another path.</p>\n<p>I used this amazing issue from the Relay repo <a href=\"https://github.com/facebook/relay/issues/541\">https://github.com/facebook/relay/issues/541</a> to derive a solution to this problem. Turns out we can use the <code>Relay.Store.getStoreData().handleQueryPayload</code> to programatically apply a payload to the store.</p>\n<p>Here’s my <code>ActionCable</code> message handler</p>\n<pre><code class=\"language-javascript\">onActionCableMessage(event) {\n  <span class=\"hljs-keyword\">const</span> pull_request = event.message.pull_request;\n\n  <span class=\"hljs-keyword\">const</span> query = Relay.createQuery(Relay.QL <span class=\"hljs-string\">`\n    query {\n      node(id: $id) {\n        ... on PullRequest {\n          title\n        }\n      }\n    }\n  `</span>, { <span class=\"hljs-attr\">id</span>: pull_request.id });\n\n  <span class=\"hljs-keyword\">const</span> payload = {\n    <span class=\"hljs-attr\">node</span>: {\n      <span class=\"hljs-attr\">title</span>: pull_request.title\n    }\n  };\n\n  Relay.Store.getStoreData().handleQueryPayload(query, payload);\n}\n</code></pre>\n<p>Basically, we build a query and it’s respective payload, and make relay update it’s store using <code>handleQueryPayload</code>. Here we simply update the pull request title for the example. Take a look at the result:</p>\n<p><img src=\"./realtimerelay.gif\" alt=\"Realtime Demo\"></p>\n<p>That’s it! We have real time updates in our Relay App using Rails ActionCable :)</p>\n<p>Let me know if you’ve found other ways of doing it or if you have any problems following the post!</p>\n<p>As always, you can find me on Twitter <a href=\"https://twitter.com/__xuorig__\">@__xuorig__</a> or <a href=\"http://github.com/xuorig\">Github</a></p>\n"}

