	module.exports = {"title":"Setting up GraphiQL in a Rails Application","date":"2016-02-20T12:00:00.000Z","description":"Learn how to setup GraphiQL in your Rails Application and test your queries without problem","body":"<meta name=\"twitter:card\" content=\"summary_large_image\">\n<meta name=\"twitter:site\" content=\"@__xuorig__\">\n<meta name=\"twitter:creator\" content=\"@__xuorig__\">\n<meta name=\"twitter:title\" content=\"Setting up GraphiQL in a Rails Application\">\n<meta name=\"twitter:description\" content=\" Learn how to setup GraphiQL in your Rails Application and test your queries without problem\">\n<meta name=\"twitter:image\" content=\"https://cloud.githubusercontent.com/assets/1919498/13198607/7ec37800-d7db-11e5-9d1b-55dcd982bd63.png\">\n<p>A cool thing that GraphQL’s Introspection brings us is the ability to make awesome tools around your schema. One of them is <a href=\"https://github.com/GraphQL/graphiql\">GraphiQL</a> (Graphical). GraphiQL enables you to test queries against you schema without the need of using curl or other tools. It has autocomplete which is really awesome, and a lot of other nice features.</p>\n<h2>Installation</h2>\n<h3>With Gem</h3>\n<p>You can use the <a href=\"https://github.com/rmosolgo/graphiql-rails\">GraphiQl-Rails</a> gem to easily mount the Graphiql endpoint in your Rails app. The steps are clearly outlined in ReadMe.</p>\n<h3>Manually</h3>\n<p>To use GraphiQL we’ll need 4 different js files in our assets. Lets just add them in <code>app/assets/javascripts</code> but you could place them wherever you want.</p>\n<h4>Graphiql.js</h4>\n<p>You can get the graphiql source right <a href=\"https://github.com/GraphQL/graphiql\">here</a>. I personally used their <code>build.sh</code> script. Clone the repo and run this script. You should then have a <code>GraphQL.js</code> and a <code>GraphQL.css</code> in the root folder. Copy the js file into your javascripts and the css file in your stylesheets.</p>\n<p>Note: I personally had to add this css rule for <code>graphiql</code> to render correctly\n{% highlight css %}\nhtml, body {\nheight: 100%;\n}</p>\n<pre><code>\nNext!\n\n<span class=\"hljs-section\">#### fetch.js</span>\n\nGraphiql needs some kind of library to call your GraphQL server. They recommend [<span class=\"hljs-string\">fetch</span>][<span class=\"hljs-symbol\">fetch</span>]. To get the <span class=\"hljs-code\">`fetch.js`</span> file, head to [<span class=\"hljs-string\">Github's fetch polyfill repo</span>][<span class=\"hljs-symbol\">github</span>] and download the <span class=\"hljs-code\">`fetch.js`</span> file. Place it in your assets too!\n\n<span class=\"hljs-section\">#### React and React-Dom</span>\n\nGraphiQL uses react components to render, so we'll need that too. Clone [<span class=\"hljs-string\">React</span>][<span class=\"hljs-symbol\">react</span>], and run <span class=\"hljs-code\">`npm install`</span>. If you haven't already, install <span class=\"hljs-code\">`grunt`</span> and <span class=\"hljs-code\">`grunt-cli`</span> globally using <span class=\"hljs-code\">`npm`</span>. Then, run <span class=\"hljs-code\">`grunt build`</span>.\n\nYou should now have a build folder containing, both <span class=\"hljs-code\">`react.js`</span> and <span class=\"hljs-code\">`react-dom.js`</span>. Add both of them to your assets!\n\nOk, now let's include all those assets in our <span class=\"hljs-code\">`application.css`</span> and <span class=\"hljs-code\">`application.js`</span> files.\n\n<span class=\"hljs-section\">#### application.css</span>\n{% highlight css %}\n/*\n = require ./graphiql\n */\n</code></pre>\n<h4>application.js</h4>\n<p>{% highlight js %}\n//= require ./react\n//= require ./react-dom\n//= require ./fetch\n//= require ./graphiql</p>\n<pre><code>\nWe have <span class=\"hljs-keyword\">finally</span> all we need <span class=\"hljs-built_in\">to</span> <span class=\"hljs-built_in\">start</span> <span class=\"hljs-keyword\">using</span> graphiql. The <span class=\"hljs-keyword\">first</span> step is <span class=\"hljs-built_in\">to</span> <span class=\"hljs-built_in\">create</span> <span class=\"hljs-keyword\">a</span> controller that will render GraphiQL.\n\n```ruby\n<span class=\"hljs-comment\"># app/controllers/graphiql_controller.rb</span>\nclass GraphiqlController &lt; ApplicationController\n  def show\n  <span class=\"hljs-function\"><span class=\"hljs-keyword\">end</span>\n<span class=\"hljs-title\">end</span></span>\n</code></pre>\n<p>As you can see the controller does nothing special, our work will be mainly in the view, in <code>app/views/graphiql/show.html.erb</code>:</p>\n<p>{% highlight html %}</p>\n<div id=\"graphiql-container\"></div>\n<script>\n// Define your graphQLFetcher (Which will call your GraphQL api)\nfunction graphQLFetcher(graphQLParams) {\n  return fetch(window.location.origin + '/queries', {\n    method: 'post',\n    headers: { 'Content-Type': 'application/json' },\n    body: JSON.stringify(graphQLParams),\n  }).then(response => response.json());\n}\nReactDOM.render(\n  React.createElement(GraphiQL, {\n    fetcher: graphQLFetcher,\n  }),\n  document.getElementById(\"graphiql-container\")\n);\n</script>\n<pre><code>\nWe render <span class=\"hljs-keyword\">the</span> `GraphiQL` React component, <span class=\"hljs-keyword\">and</span> pass <span class=\"hljs-keyword\">it</span> our fetcher, which will <span class=\"hljs-built_in\">post</span> <span class=\"hljs-built_in\">to</span> our `queries_controller`. (Replace this <span class=\"hljs-keyword\">by</span> wherever you execute GraphQL queries <span class=\"hljs-keyword\">in</span> your App.)\n\nFinally, lets <span class=\"hljs-built_in\">add</span> <span class=\"hljs-keyword\">a</span> route so we can access GraphiQL. In `routes.rb` <span class=\"hljs-built_in\">add</span> this <span class=\"hljs-built_in\">line</span>:\n\n```ruby\n  <span class=\"hljs-built_in\">get</span> <span class=\"hljs-string\">'graphiql'</span>, <span class=\"hljs-built_in\">to</span>: <span class=\"hljs-string\">'graphiql#show'</span>\n</code></pre>\n<p>Now head to <code>/graphiql</code> and you should see the GraphiQL interface! Time to test your queries :)</p>\n<img width=\"595\" alt=\"screenshot 2016-02-20 14 06 19\" src=\"https://cloud.githubusercontent.com/assets/1919498/13198607/7ec37800-d7db-11e5-9d1b-55dcd982bd63.png\">\n<p>You can see an example of this setup at my <a href=\"https://github.com/xuorig/my-simple-blogging-app\">Simple Blogging App</a> repo.</p>\n<p>You can find me on Twitter <a href=\"https://twitter.com/__xuorig__\">@__xuorig__</a> or <a href=\"http://github.com/xuorig\">Github</a></p>\n"}

